@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  font-family: "Open Sans", serif;
}

.wrapper {
  background: linear-gradient(to right, #30798a, #e39d9a);
  height: 100vh;
  align-items: center;
  justify-content: center;
  display: flex;
}

.main {
  background: #2b6c7b;
  box-shadow: 0px 13px 40px -13px rgba(100, 160, 178);
  height: 90vh;
  width: 80%;
  margin: 0 auto;
}

.title-container {
  color: rgba(0, 0, 0, 0.774);
  background: url(/React-in-github-weather/static/media/bg.ab11d1a6.jpg) center center no-repeat;
  background-size: cover;
  height: 90vh;
  align-items: center;
  justify-content: center;
  display: flex;
  text-align: center;
}

.title-container__title {
  font-family: 'Arial Black', serif;
  font-size: 50px;
  letter-spacing: 2px;
  line-height: 1.3;
}

.title-container__subtitle {
  font-family: "Verdana", serif;
  font-weight: 100;
  letter-spacing: 1px;
  line-height: 1.5;
}

.form-container {
  background-color: #ab576e;
  height: 90vh;
  padding-top: 100px;
  padding-left: 50px;
}
input[type="text"] {
  background-color: transparent;
  width: 30%;
  color: #fff !important;
  font-size: 20px; 
  font-weight: lighter;
  border-bottom: solid 1px #fea497;
  border: 0;
  padding-bottom: 4px;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
}

input[type="text"] { 
    outline: none;
}

input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px #ab576e inset;
    -webkit-text-fill-color: #fff !important;
}

button {
  background-color: #7bb4c5;
  color: #fff;
  font-size: 15px;
  font-weight: lighter;
  font-weight: 100;
  margin: 0 2px;
  border: 0;
  border-radius: 2px;
  padding: 8px 20px;
  letter-spacing: 1px;
  cursor: pointer;
}

button:active {
  outline: none;
}

.weather__info {
  width: 60%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
}

.weather__key {
  color: #000;
  border-bottom: solid 2px rgb(247, 158, 147);
  font-weight: 400;
  padding: 20px 0 20px 0;
}

.weather__key:last-child {
  border: 0;
}

.weather__value {
  color: #fff;
  font-weight: 200;
}

.weather__error {
  color: #fff;
  font-weight: 200;
  font-size: 20px;
  letter-spacing: 1px;
}
